import React from 'react'

class App1 extends React.Component {

    constructor() {
        super()
        this.state = {
            isLoggedIn: true
        }
    }
    render() {
        let wordDisplay
        if (this.state.isLoggedIn === true) {
            wordDisplay = "in"
        } else {
            wordDisplay = "out"
        }
        return (
            <div>
                You are now {wordDisplay}
            </div>
        )
    }
}

export default App1